@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  font-family: 'Roboto', sans-serif;
}
.back-tres{
  background-color: #00B18F;
}

.back-uno{
    background-color: #f3f3f3;
    border: 3px solid #00B18F;
    border-radius: 20px;
    margin: 5px;
    text-align: center;
}
