}

.back-uno{
    background-color: #f3f3f3;
    border: 3px solid #00B18F;
    border-radius: 20px;
    margin: 5px;
    text-align: center;
}
h3{
    margin: 15px;
    color: #CC0001; 
}
button{
    margin: 15px;
    background-color: #282c34;
    color: white;
    border-radius: 5px;
    padding: 5px 10px;
}
.bodrow{
    display: flex;
    flex-wrap: wrap;
}

.blocks{
    display: flex;
    flex-wrap: wrap;
    margin: 10px 0px 15px;
}

.img-menu{
    width: 80%;
    border-radius: 5px;
    margin-bottom: 15px;
}
p{
    font-weight: 600;
    margin: 0 4px;
}

.product{
  border-radius: 5px;
  margin: 1%;
  font-size: 13px;
  width: 30%;
  padding: 10px 0px;
  color: black;
  font-family: inherit;
  background-color: #FFBE00;
}
.product:hover{
  box-shadow: 0px 4px 10px rgba(20, 43, 127, 0.25);
}

.input-client{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 50%;
}

.input-client input{
  border-radius: 8px;
  padding: 12px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: 1px solid #FFBE00;
  width: 100%;
}

.error-input{
  color: red;
  font-size: 16px;
}

.input-client:focus{
  outline: none;
}
.input-client:hover{
  box-shadow: 0px 4px 10px rgba(20, 43, 127, 0.25);
}

.head{
    border-bottom-style: double;
    font-weight: 1000;
}

.sign{
    font-size: 8px;
    color: white;
    height: 18px;
    width: 18px;
    background-color: #282c34;
    padding: 5px;
    border-radius: 3px;
    margin: 0 5px 0  5px;
    border-style: none;
}

i{
    margin: 0px;
}
.total-price{
    margin: 30px;
    font-weight: 600;
    border-style: groove;
    border-radius: 6px;
}

.prod{
    margin-top: 10px;
}

.section-client-name{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 24px 0;
}

.empty-box{
  padding: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.empty-box p{
  font-size: 24px;
  margin: 16px;
}

.empty-box img{
  width: 320px;
}

.btn-primary{
  margin: 15px;
  background-color: #282c34;
  color: white;
  border-radius: 5px;
  padding: 5px 10px;
  outline: none;
  text-transform: uppercase;
  font-size: 14px;
}

.btn-primary:hover {
  background-color: #FFBE00;
}

.btn-secondary{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 60px;
  background-color: #FFBE00;
  margin: 24px;
  border-radius: 15px;
  outline: none;
  color: #282c34;
}

.btn-secondary:hover{
  background-color: #00B18F;
}

.btn-secondary > a {
  color: #282c34;
  text-decoration: none;
}

.btn-primary:focus, .btn-secondary:focus {
  outline: none;
}

.header{
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  text-align: center;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  padding: 16px 24px;
}

.main-title{
  font-size: 40px;
  color: white;
}

.icon-back{
  width: auto;
  font-size: 10px;
  background-color: azure;
  border-radius: 5px;
  padding: 5px;
}


.ped{
    border-style: groove;
    border-radius: 10px;
    width: 331px;
    text-align: -webkit-center;
    padding: 10px;
    margin: 5px;
    font-weight: 1000;
}
.pedidos-fire{
    margin: 10px 0;
    font-size: 18px;
}
.title{
    font-size: 20px;
    font-weight: 1000;
}
.hora-pedido{
    font-size: 15px;
}
.head-pedidos{
    border-bottom: groove;
}
hr{
    border-top: 2px solid #FFBE00;
}
.inicio{
    background-image: url(https://user-images.githubusercontent.com/45070947/56369372-d5ca5b80-61be-11e9-9f0c-e5d9b0e37d08.png);
    background-attachment: fixed;
    background-size:cover;
    height: calc(100vh - 140px);
    display: flex;
}

a{
    color: #282c34;
    font-weight: 800;
    font-size: 20px;
}

